// Styling for the footer
.footer {
  color: white;
  background-color: $gray-900;
  padding: 25px 0;
  text-align: center;
  span.copyright {
    font-size: 90%;
    line-height: 40px;
    text-transform: none;
    @include heading-font;
  }
  ul.quicklinks {
    font-size: 90%;
    line-height: 40px;
    margin-bottom: 0;
    text-transform: none;
    @include heading-font;
  }
}

ul.social-buttons {
  margin-bottom: 0;
  li {
    a {
      font-size: 20px;
      line-height: 50px;
      display: block;
      width: 50px;
      height: 50px;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      transition: all 0.3s;
      color: $gray-900;
      border-radius: 100%;
      outline: none;
      background-color: $white;
      &:active,
      &:focus,
      &:hover {
        background-color: $primary;
      }
    }
  }
}

img.flag {
  display: block;
  max-width:230px;
  max-height:95px;
  width: auto;
  height: auto;
}

